@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button {
  cursor: pointer;
  border: 1px solid #1a202c;
  padding: 8px;
  min-width: 64px;

  background: transparent;

  transition: all 0.1s ease-in;
}

.button:hover {
  background: #1a202c;
  color: #ffffff;
}

.addTextInput {
  font-size: 20px;
  color: black;
  border-color: green;
  background: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.checkbox{position:relative;width:1.5em;height:1.5em;color:black;border:1px solid gray;border-radius:4px;-webkit-appearance:none;appearance:none;outline:0;cursor:pointer;transition:background 175ms cubic-bezier(0.1, 0.1, 0.25, 1)}.checkbox::before{position:absolute;content:"";display:block;top:2px;left:7px;width:8px;height:14px;border-style:solid;border-color:white;border-width:0 2px 2px 0;-webkit-transform:rotate(45deg);transform:rotate(45deg);opacity:0}.checkbox:checked{color:white;border-color:blue;background:white}.checkbox:checked::before{opacity:1;border-color:green}.checkbox:checked ~ label::before{-webkit-clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%);clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%)}.label{position:relative;cursor:pointer;font-size:1.5em;font-weight:600;padding:0 0.25em 0;-webkit-user-select:none;-ms-user-select:none;user-select:none}.label::before{position:absolute;content:attr(data-content);color:yellowgreen;-webkit-clip-path:polygon(0 0, 0 0, 0% 100%, 0 100%);clip-path:polygon(0 0, 0 0, 0% 100%, 0 100%);text-decoration:line-through;text-decoration-thickness:2px;-webkit-text-decoration-color:black;text-decoration-color:black;transition:-webkit-clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94)}

.root {
  background-color: yellow;
}

.jw-modal{position:fixed;top:0;right:0;bottom:0;left:0;z-index:1000;overflow:auto}.jw-modal .jw-modal-body{padding:20px;background:#fff;margin:40px}.jw-modal-background{position:fixed;top:0;right:0;bottom:0;left:0;background-color:#000;opacity:0.75;z-index:900}.jw-modal-open{overflow:hidden}

